er !important;\n}\n.text-black{\n  color: $light-black;\n}\n\n.blockquote{\n    border-left: none;\n    border: 1px solid $default-color;\n    padding: 20px;\n    font-size: $font-size-blockquote;\n    line-height: 1.8;\n\n    small{\n        color: $default-color;\n        font-size: $font-size-small;\n        text-transform: uppercase;\n    }\n\n    &.blockquote-primary{\n        border-color: $primary-color;\n        color: $primary-color;\n\n        small{\n            color: $primary-color;\n        }\n    }\n\n    &.blockquote-danger{\n        border-color: $danger-color;\n        color: $danger-color;\n\n        small{\n            color: $danger-color;\n        }\n    }\n\n    &.blockquote-white{\n        border-color: $opacity-8;\n        color: $white-color;\n\n        small{\n            color: $opacity-8;\n        }\n    }\n}\n","body{\n    color: $black-color;\n    font-size: $font-size-base;\n    font-family: $sans-serif-family;\n    overflow-x: hidden;\n    -moz-osx-font-smoothing: grayscale;\n    -webkit-font-smoothing: antialiased;\n}\n\n.main{\n    position: relative;\n    background: $white-color;\n}\n/* Animations */\n.nav-pills .nav-link,\n.nav-item .nav-link,\n.navbar,\n.nav-tabs .nav-link,\n.tag,\n.tag [data-role=\"remove\"]{\n    @include transition($general-transition-time, $transition-ease);\n}\n\n//transition for dropdown caret\n.card a,\n.bootstrap-switch-label:before{\n    @include transition($fast-transition-time, $transition-ease);\n}\n\n.dropdown-toggle:after,\n[data-toggle=\"collapse\"][data-parent=\"#accordion\"] i{\n  @include transition-on-prop(transform, $fast-transition-time, $transition-ease);\n}\n\n.dropdown-toggle[aria-expanded=\"true\"]:after,\n[data-toggle=\"collapse\"][data-parent=\"#accordion\"][aria-expanded=\"true\"] i{\n    @include rotate-180();\n}\n\n.button-bar{\n    display: block;\n    position: relative;\n    width: 22px;\n    height: 1px;\n    border-radius: 1px;\n    background: $white-bg;\n\n    & + .button-bar{\n        margin-top: 7px;\n    }\n\n    &:nth-child(2){\n        width: 17px;\n    }\n}\n\n.separator-line{\n    height: 2px;\n    width: 44px;\n    background-color: $default-color;\n    margin: 20px auto;\n\n    &.separator-primary{\n        background-color: $primary-color;\n    }\n}\n\n.section-space{\n    height: 62px;\n    display: block;\n}\n\n.pull-left{\n  float: left;\n}\n.pull-right{\n  float: right;\n}\n\n.title-up{\n  text-transform: uppercase;\n}\n",".btn,\n.navbar .navbar-nav > a.btn{\n    border-width: $border-thick;\n    font-weight: $font-weight-normal;\n    font-size: $font-size-small;\n    line-height: $line-height;\n    margin: 10px 1px;\n    border: none;\n    border-radius: $border-radius-small;\n    padding: $padding-btn-vertical $padding-btn-horizontal;\n    cursor: pointer;\n\n    @include btn-styles($default-color, $default-states-color);\n\n    &:hover,\n    &:focus{\n        @include opacity(1);\n        outline: 0 !important;\n    }\n    &:active,\n    &.active,\n    .open > &.dropdown-toggle {\n         @include box-shadow(none);\n         outline: 0 !important;\n    }\n\n    &.btn-icon {\n        // see above for color variations\n        height: $btn-icon-size-regular;\n        min-width: $btn-icon-size-regular;\n        width: $btn-icon-size-regular;\n        padding: 0;\n        font-size: $btn-icon-font-size-regular;\n        overflow: hidden;\n        position: relative;\n        line-height: normal;\n\n        &[class*=\"btn-outline-\"]{\n            padding: 0;\n        }\n\n        &.btn-sm{\n            height: $btn-icon-size-small;\n            min-width: $btn-icon-size-small;\n            width: $btn-icon-size-small;\n\n            i.fab,\n            i.now-ui-icons{\n                font-size: $btn-icon-font-size-small;\n            }\n        }\n\n        &.btn-lg{\n            height: $btn-icon-size-lg;\n            min-width: $btn-icon-size-lg;\n            width: $btn-icon-size-lg;\n\n            i.now-ui-icons,\n            i.fab{\n                font-size: $btn-icon-font-size-lg;\n            }\n        }\n\n        &:not(.btn-footer) i.now-ui-icons,\n        &:not(.btn-footer) i.fab{\n            position: absolute;\n            top: 50%;\n            left: 50%;\n            transform: translate(-12px, -12px);\n            line-height: 1.5626rem;\n            width: 23px;\n        }\n\n    }\n\n    &:not(.btn-icon) .now-ui-icons{\n        position: relative;\n        top: 1px;\n    }\n}\n\n// Apply the mixin to the buttons\n// .btn-default { @include btn-styles($default-color, $default-states-color); }\n.btn-primary { @include btn-styles($primary-color, $primary-states-color); }\n.btn-success { @include btn-styles($success-color, $success-states-color); }\n.btn-info    { @include btn-styles($info-color, $info-states-color); }\n.btn-warning { @include btn-styles($warning-color, $warning-states-color); }\n.btn-danger  { @include btn-styles($danger-color, $danger-states-color); }\n.btn-neutral { @include btn-styles($white-color, $white-color); }\n\n.btn-outline-primary { @include outline-buttons($primary-color, $primary-states-color); }\n.btn-outline-success { @include outline-buttons($success-color, $success-states-color); }\n.btn-outline-info    { @include outline-buttons($info-color, $info-states-color); }\n.btn-outline-warning { @include outline-buttons($warning-color, $warning-states-color); }\n.btn-outline-danger  { @include outline-buttons($danger-color, $danger-states-color); }\n.btn-outline-default { @include outline-buttons($default-color, $default-states-color); }\n\n.btn-round{\n    border-width: $border-thin;\n    border-radius: $btn-round-radius !important;\n    padding: $padding-btn-vertical  $padding-round-horizontal;\n\n    &[class*=\"btn-outline-\"]{\n        padding: $padding-btn-vertical - 1  $padding-round-horizontal - 1;\n    }\n}\n[class*=\"btn-outline-\"]{\n    border: $border;\n    padding: $padding-btn-vertical - 1 $padding-round-horizontal - 1;\n    background-color: $transparent-bg;\n}\n\n[class*=\"btn-outline-\"],\n.btn-link{\n    &.disabled,\n    &:disabled,\n    &[disabled],\n    fieldset[disabled] & {\n        &,\n        &:hover,\n        &:focus,\n        &.focus,\n        &:active,\n        &.active {\n            background-color: $transparent-bg;\n        }\n    }\n}\n\n.btn-lg{\n   @include btn-size($padding-large-vertical, $padding-large-horizontal, $font-size-large, $border-radius-large);\n}\n.btn-sm{\n    @include btn-size($padding-small-vertical, $padding-small-horizontal, $font-size-base, $border-radius-small);\n}\n\n.btn-link{\n    border: $none;\n    padding: $padding-base-vertical $padding-base-horizontal;\n    background-color: $transparent-bg;\n}\n\n.btn-wd {\n    min-width: 140px;\n}\n.btn-group.select{\n    width: 100%;\n}\n.btn-group.select .btn{\n    text-align: left;\n}\n.btn-group.select .caret{\n    position: absolute;\n    top: 50%;\n    margin-top: -1px;\n    right: 8px;\n}\n","// Mixin for generating new styles\n@mixin btn-styles($btn-color, $btn-states-color) {\n    background-color: $btn-color;\n\n\n\n    &:hover,\n    &:focus,\n    &:not(:disabled):not(.disabled):active,\n    &:not(:disabled):not(.disabled).active,\n    &:not(:disabled):not(.disabled):active:focus,\n    &:not(:disabled):not(.disabled).active:focus,\n    &:active:hover,\n    &.active:hover,\n    .show > &.dropdown-toggle,\n    .show > &.dropdown-toggle:focus,\n    .show > &.dropdown-toggle:hover {\n        background-color: $btn-states-color;\n        color: $white-color;\n        box-shadow: none;\n    }\n\n    &:hover{\n        box-shadow:  0 3px 8px 0 rgba(0,0,0, 0.17);\n    }\n\n    &.disabled,\n    &:disabled,\n    &[disabled],\n    fieldset[disabled] & {\n        &,\n        &:hover,\n        &:focus,\n        &.focus,\n        &:active,\n        &.active {\n            background-color: $btn-color;\n            border-color: $btn-color;\n        }\n    }\n\n    // btn-neutral style\n    @if $btn-color ==