dal-info{\n        @include modal-colors($info-color, $white-color);\n    }\n\n    &.show.modal-mini .modal-dialog{\n        -webkit-transform: translate(0,30%);\n        -o-transform: translate(0,30%);\n        transform: translate(0,30%);\n    }\n\n    .modal-header .close{\n        color: $danger-color;\n        text-shadow: none;\n\n        &:hover,\n        &:focus{\n            opacity: 1;\n        }\n    }\n}\n","@mixin modal-colors($bg-color, $color) {\n    .modal-content{\n        background-color: $bg-color;\n        color: $color;\n    }\n\n    .modal-header .close{\n        color: $color;\n    }\n\n\n}\n",".modal{\n  .modal-login{\n      max-width: 320px;\n\n      .card-login{\n          .logo-container{\n              width: 65px;\n              margin-bottom: 38px;\n              margin-top: 27px;\n          }\n      }\n  }\n}\n","// display flex was causing errors\n.carousel-item-next,\n.carousel-item-prev,\n.carousel-item.active{\n    display: block;\n}\n\n.carousel{\n    .carousel-inner{\n        box-shadow: $box-shadow-raised;\n        border-radius: $border-radius-small;\n    }\n\n    .card {\n        max-width: 650px;\n        margin: 60px auto;\n        display: block;\n    }\n}\n\n.carousel-control-next,\n.carousel-control-prev {\n  z-index: 9999;\n  cursor: pointer;\n}\n",".footer{\n    padding: 1.125rem 0;\n    text-align: center;\n\n    &.footer-default{\n        background-color: #f2f2f2;\n    }\n\n    &.footer-white{\n        background-color: $white-color;\n    }\n\n    nav{\n        display: inline-block;\n        float: left;\n    }\n\n    .content{\n        text-align: left;\n    }\n\n    .nav-link{\n        display: inline;\n    }\n\n    &.footer-big{\n        padding: 30px 0;\n\n        .social-feed i{\n            font-size: 20px;\n            float: left;\n            padding-right: 10px;\n            padding-bottom: 30px;\n        }\n\n        .gallery-feed img{\n            width: 20%;\n            margin-right: 5%;\n            