\"]{\n      background-color: $primary-color;\n  }\n\n  &[data-background-color=\"red\"]{\n      background-color: $danger-color;\n  }\n\n  &[data-background-color=\"yellow\"]{\n      background-color: $warning-color;\n  }\n\n  &[data-background-color=\"blue\"]{\n      background-color: $info-color;\n  }\n\n  &[data-background-color=\"green\"]{\n      background-color: darken($success-color, 5%);\n  }\n}\n",".card{\n  .rounded{\n      border-radius: $border-radius-small !important;\n  }\n\n  &:not(.card-plain) .rounded{\n      border-bottom-left-radius: 0 !important;\n      border-bottom-right-radius: 0 !important;\n  }\n\n  a[data-toggle='collapse']{\n      text-decoration: none;\n  }\n\n  & a:not(.btn):not(.nav-link):not([data-toggle='collapse']):not(.footer-link){\n      text-decoration: none;\n      border-bottom: 2px solid transparent;\n      color: $light-black;\n\n      &:hover,\n      &:focus{\n          border-color: #444;\n      }\n  }\n\n  .card-header{\n      .card-collapse &{\n          padding: .75rem 0;\n          position: relative;\n\n          &:after{\n              content: \"\";\n              position: absolute;\n              bottom: 0 ;\n              width: 100%;\n              height: 1px;\n              background-color: $light-gray;\n          }\n\n          a[data-toggle=\"collapse\"]{\n              display: block;\n              color: $light-black;\n\n\n              &[aria-expanded=\"true\"],\n              &.expanded{\n                  i{\n                      @include rotate-180();\n                  }\n              }\n\n              i{\n                  float: right;\n                  position: relative;\n                  color: $primary-color;\n                  top: 1px;\n                  @include transition($general-transition-time, $transition-ease);\n              }\n          }\n      }\n  }\n\n  .card-image{\n      position: relative;\n  }\n\n  .card-footer{\n      div{\n          display: inline-block;\n      }\n\n      .stats{\n          &.stats-right{\n              float: right;\n              line-height: 30px;\n          }\n\n          span:first-child{\n              margin-right: 30px;\n          }\n      }\n  }\n\n  &:not(.card-product) .card-footer .stats{\n      .now-ui-icons{\n          position: relative;\n          top: 3px;\n          font-size: 18px;\n          margin-right: 5px;\n\n      }\n\n      i{\n          color: lighten($default-color, 10%);\n      }\n  }\n\n  .card-title{\n      margin-top: 15px;\n      line-height: 1.25em;\n  }\n\n  .author{\n      .avatar{\n          width: 30px;\n          height: 30px;\n          overflow: hidden;\n          border-radius: 50%;\n          margin-right: 5px;\n      }\n      span{\n          color: $dark-gray;\n      }\n  }\n\n\n  .info{\n      padding: 40px 0 40px;\n      text-align: center;\n      position: relative;\n      z-index: 2;\n\n  }\n}\n","\n.card-background{\n    background-position: center center;\n    background-size: cover;\n    text-align: center;\n\n    .card-body{\n        position: relative;\n        z-index: 2;\n        min-height: 370px;\n        max-width: 530px;\n        margin: 0 auto;\n        padding-top: 60px;\n        padding-bottom: 60px;;\n    }\n\n    &.card-background-product .card-body{\n        max-width: 400px;\n        .card-title{\n            margin-top: 30px;\n        }\n    }\n\n    .stats{\n        color: $white-color;\n    }\n\n    .card-footer{\n        .stats-link > a{\n            color: $white-color;\n            line-height: 1.9;\n        }\n    }\n\n    .category,\n    .card-description,\n    small{\n        color: $opacity-8;\n    }\n\n    .card-title{\n        color: $white-color;\n        margin-top: 130px;\n    }\n\n    &:not(.card-pricing) .btn{\n        margin-bottom: 0;\n    }\n\n    &:after{\n        position: absolute;\n        z-index: 1;\n        width: 100%;\n        height: 100%;\n        display: block;\n        left: 0;\n        top: 0;\n        content: \"\";\n        background-color: rgba(0,0,0,.63);\n        border-radius: $border-radius-large;\n    }\n}\n",".card-blog{\n    .row{\n        .category{\n            margin-bottom: 0;\n        }\n    }\n\n    .card-image + .category{\n        margin-top: 20px;\n    }\n}\n",".card-contact{\n    .header{\n        .title{\n            margin: 10px 0 10px;\n        }\n    }\n}\n",".card-fashion{\n    min-height: 500px;\n    border-radius: 0;\n    box-shadow: none;\n    margin:0;\n    display: block;\n\n    .card-container &{\n        width: 50%;\n        float: left;\n    }\n\n    &:not(.card-background) .card-title{\n        position: absolute;\n        top: 50%;\n        left: 50%;\n        -webkit-transform: translate(-50%, -50%);\n        transform: translate(-50%, -50%);\n        text-align: center;\n        width: 85%;\n    }\n\n    h4{\n        margin-top: 0;\n    }\n\n    &.card-background{\n        &::after{\n            border-radius: 0;\n        }\n\n        a{\n            color: $white-color !important;\n\n            &:hover,\n            &:focus{\n                border-color: $white-color !important;\n            }\n        }\n    }\n\n    &:not(.card-background) .stats span,\n    .stats-link{\n        color: initial;\n    }\n\n    .card-body{\n        position: absolute;\n        bottom: 0;\n        left: 0;\n        right: 0;\n        padding: 2rem 2rem 1.4rem;\n        color: #fff;\n        min-height: auto;\n    }\n\n    &:not(.card-background):before{\n        left: 100%;\n        top: 50%;\n        border: solid transparent;\n        content: \" \";\n        height: 0;\n        width: 0;\n        z-index: 2;\n        position: absolute;\n        pointer-events: none;\n        border-color: rgba(255,255,255,0);\n        border-left-color: #ffffff;\n        border-width: 10px;\n        margin-top: -10px;\n    }\n\n    &.arrow-left:not(.card-background):before{\n        right: 100%;\n        border-right-color: #fff;\n        left: auto;\n        border-left: none;\n    }\n\n}\n",".card-form-horizontal{\n    .card-body{\n        padding-left: 15px;\n        padding-right: 15px;\n    }\n    .form-group{\n        padding-bottom: 0px;\n        margin: 3px 0 0 0;\n\n        .form-control{\n            margin-bottom: 0;\n        }\n    }\n    .btn{\n        margin: 0;\n    }\n    .input-group,\n    .form-group{\n        margin: 0;\n    }\n}\n",".card-no-shadow{\n    box-shadow: none;\n    border-radius: 0;\n    margin: 0;\n\n    &:after{\n        border-radius: 0;\n    }\n}\n",".card-plain{\n    background: transparent;\n    box-shadow: none;\n\n    .card-body,\n    .card-header{\n        padding-left: 5px;\n        padding-right: 5px;\n    }\n\n    .card-header:after{\n        width: 100%;\n        left: 0;\n    }\n}\n","\n.card-pricing{\n    text-align: center;\n\n    .card-title{\n        margin-top: 30px;\n    }\n\n    .icon{\n        padding: 10px 0 0px;\n\n        i{\n            font-size: 30px;\n            line-height: 2.7;\n            max-width: 80px;\n            color: $default-color;\n            width: 80px;\n            height: 80px;\n            margin: 0 auto;\n            border-radius: 50%;\n            box-shadow: 0px 9px 35px -6px rgba(0, 0, 0, 0.3);\n            background-color: $white-color;\n            position: relative;\n        }\n\n        &.icon-primary i{\n            @include icon-color($primary-color);\n        }\n        &.icon-info i{\n            @include icon-color($info-color);\n        }\n        &.icon-success i{\n            color: $success-color;\n            box-shadow:  0px 9px 30px -6px rgba(22, 199, 13, 0.85);\n        }\n        &.icon-warning i{\n            @include icon-color($warning-color);\n        }\n        &.icon-danger i{\n            @include icon-color($danger-color);\n        }\n    }\n    h1{\n        small{\n            font-size: 18px;\n\n            &:first-child{\n                position: relative;\n                top: -17px;\n                font-size: 26px;\n            }\n        }\n    }\n\n    ul{\n        list-style: none;\n        padding: 0;\n        max-width: 240px;\n        margin: 10px auto;\n\n        li{\n            color: $default-color;\n            text-align: center;\n            padding: 12px 0;\n            border-bottom: 1px solid rgba($default-color,.3);\n\n            &:last-child{\n                border: 0;\n            }\n            b{\n                color: $black-color;\n            }\n            i{\n                top: 3px;\n                right: 3px;\n                position: relative;\n                font-size: 20px;\n            }\n        }\n    }\n\n    &.card-background{\n        ul{\n            li{\n                color: $white-color;\n                border-color: rgba($white-color,.3);\n\n                b{\n                    color: $white-color;\n                }\n            }\n        }\n        [class*=\"text-\"]{\n            color: $white-color !important;\n        }\n        .card-body{\n            padding-top: 1.25rem;\n            padding-bottom: 1.25rem;\n        }\n    }\n    &.card-background:after{\n        background-color: rgba(0, 0, 0, 0.65);\n    }\n}\n","@mixin icon-color($color) {\n    box-shadow:  0px 9px 30px -6px $color;\n    color: $color;\n}\n","%card-profile{\n    margin-top: 60px;\n    text-align: center;\n\n    .card-body .card-title{\n        margin-top: 0;\n    }\n\n     [class*=col-]{\n        .card-description{\n            margin-bottom: 0;\n\n            & + .card-footer{\n                margin-top: 8px;\n            }\n        }\n\n\n     }\n\n    .card-avatar{\n        max-width: 130px;\n        max-height: 130px;\n        margin: -60px auto 0;\n\n        img{\n            border-radius: 50% !important;\n        }\n\n        & + .card-body{\n            margin-top: 15px;\n        }\n    }\n\n    &.card-plain{\n        .card-avatar{\n            margin-top: 0;\n        }\n    }\n    .card-body{\n        .card-avatar{\n            margin: 0 auto 30px;\n        }\n    }\n}\n\n.card-profile{\n\t@extend %card-profile;\n}\n","\n.card-product{\n    margin-top: 30px;\n\n    .price-container{\n        margin-top: 9px;\n    }\n\n    .btn{\n        margin: 0;\n    }\n\n    .image{\n        overflow: hidden;\n        border-radius: $border-radius-large;\n    }\n\n    .card-footer{\n        margin-top: 5px;\n        .price{\n            h4{\n                margin-bottom: 0;\n                margin-top: 0;\n            }\n        }\n        .stats{\n            margin-top: 5px;\n\n            .btn{\n                position: relative;\n                top: -3px;\n            }\n        }\n    }\n\n    .card-title,\n    .category,\n    .card-description{\n        text-align: center;\n    }\n    .category{\n        margin-bottom: 0;\n    }\n    .category ~ .card-title{\n        margin-top: 0;\n    }\n\n    .price{\n        font-size: 16px;\n        color: $default-color;\n    }\n    .price-old{\n        text-decoration: line-through;\n        font-size: 16px;\n        color: $default-color;\n    }\n\n    .price-new{\n        color: $danger-color;\n    }\n\n    &:not(.card-plain) .card-image{\n        padding: 15px;\n    }\n}\n",".card-raised {\n  box-shadow: 0px 20px 50px 0px rgba(0, 0, 0, 0.3);\n\n  &.card-plain{\n    box-shadow: none;\n  }\n}\n",".card-signup{\n    max-width: 350px;\n    margin: 0 auto;\n\n    .card-header{\n      margin:0 20px;\n      padding: 30px 0;\n    }\n\n    .card-title{\n      margin-top: 15px;\n      margin-bottom: 15px;\n    }\n\n    .card-footer {\n      margin-bottom: 10px;\n      margin-top: 24px;\n      padding: 24px 0;\n    }\n\n    .card-body{\n        padding-top: 0px;\n        padding-bottom: 0px;\n        min-height: auto;\n    }\n\n    .form-check,\n    .form-check.form-check-radio{\n        margin-top: 20px;\n\n        label{\n            margin-left: 11px;\n            color: initial;\n            padding-left: 40px;\n        }\n    }\n\n    .card-descripti